a{
    text-decoration: none;
   
    
     
}

li {
  list-style: none;
  text-decoration: none;  
 
  
}


 
/* navbar style start  */

.main-nav {
  width: 100%;
  height: 8rem;
  display: grid;
  align-items: center;
 
  grid-template-columns: 10rem 2fr 1fr 10rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px ,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;

    
    
}

/* .logo {
  display: grid;
   background-color: #3b5998;
  grid-column: 2/3;
  justify-content: start;
  align-items: center;
} */

.menu-link {
  grid-column: 2/3;
 
  
}
  
  


.menu-link ul {
  height:5rem;
  display: flex;
  justify-content: space-around;
  align-items: center; 
  padding: 2px 0;
}

.social-media {
  grid-column: 3/4;
  
}

.social-media ul {
  height: 10rem;
  display: grid;
  grid-template-columns: 3fr repeat(3, 1fr);
  align-items: center;
  justify-content: flex-end;
}

.social-media ul li {
  text-align: right;
}

.social-media ul li:first-child {
  grid-column: 2/3;
}

/* ----------- Grid part ends ----------------- */

